<template>
  <div class="register-container">
    <p class="title">
     注册
    </p>
    <form action="" @submit.prevent="submit" class="comForm" v-if="!succeedShow">
      <div class="input-wrapper">
        <input type="text" v-model="registerForm.idNum"  @focus="focus($event)" @blur="blur($event)" placeholder="身份证号">
        <i class="del" v-show="registerForm.idNum" @mousedown.stop="del('idNum')" ></i>
      </div>
      <div class="input-wrapper">
        <input type="text" v-model="registerForm.queryName"  @focus="focus($event)" @blur="blur($event)" placeholder="姓名">
        <i class="del" v-show="registerForm.queryName" @mousedown.stop="del('queryName')" ></i>
      </div>
      <div class="input-wrapper">
        <input type="text" v-model="registerForm.queryMobile"  @focus="focus($event)" @blur="blur($event)" placeholder="手机号">
        <i class="del" v-show="registerForm.queryMobile" @mousedown.stop="del('queryMobile')" ></i>
      </div>
      <div class="input-wrapper msgcode-wrapper clearfix">
        <input type="text" v-model="registerForm.msgCode"  @focus="focus($event)" @blur="blur($event)" placeholder="短信验证码">
        <i class="del" v-show="registerForm.msgCode" @mousedown.stop="del('msgCode')"></i>
        <span class="aboutcode" v-if="!isMessageCode" @click="getMessageCode()">发送验证码</span>
        <span class="aboutcode sendcode" v-else>已发送({{second}})</span>
      </div>
      <div class="submit-wrapper">
        <p class="register-tip">注册即视为同意<span>《会员注册协议》</span></p>
        <div class="error-tips" v-show="error">{{error}}</div>
        <input type="submit" value="注册" class="submit" :class="allValid? 'active':''" :disabled="!allValid">
      </div>
      <div class="go-login-tips">
        已有账号，马上<router-link tag="span" to="/personCenter/smsLogin" class="register-btn"><span>登录</span></router-link>
      </div>
    </form>
    <base-succeed :successTitle="'注册成功'" :firstBtnTitle="'设置密码'" :secondBtnTitle="'返回首页'" v-if="succeedShow"></base-succeed>
  </div>
</template>

<script>
// import { ValidateUtil } from 'common/js/global'
import api from '@/api'
import BaseSucceed from '@/components/BaseSucceed'
// import { uuid } from '@/assets/js/util'
import { getComForm } from '@/assets/js/mixins/comForm'
import { mapActions } from 'vuex'

export default {
  name: 'login',
  mixins: [getComForm],
  data () {
    return {
      error: '',
      isMessageCode: false,
      registerForm: {
        idNum: '',
        queryName: '',
        queryMobile: '',
        msgCode: ''
      }
    }
  },
  mounted () {
    this.getList()
    // var cc = uuid()
    console.log('ff')
  },
  methods: {
    getList () {
      api.getProductList().then(res => {
        console.log(12, res)
      })
    },
    getMessageCode () {
      this.error = '输入不得为空'
    },
    submit () {
      // else if (!ValidateUtil.idCardRegexs(this.loginForm.idNum)) {
      //     this.error = '身份证格式错误'
      //   }
      if (!this.loginForm.idNum || !this.loginForm.msgCode) {
        this.error = '输入不得为空'
      } else {
        // this.$store.dispatch('Logins', this.loginForm)
        this.Logins(this.loginForm)
      }
    },
    del (type) {
      this.loginForm[type] = ''
    },
    ...mapActions([
      'Logins'
    ])
  },
  computed: {
    allValid () {
      return this.registerForm.idNum && this.registerForm.queryName &&
        this.registerForm.queryMobile && (this.registerForm.msgCode.length >= 4)
    }
  },
  components: {
    BaseSucceed
  }

}
</script>
<style scoped lang="scss">
  @import "@/assets/styles/comForm.scss";
  .register-container{
    .title{
      font-size: 32px;
      color: #1C1D1D;
      font-weight: bold;
      padding-top: 50px;
    }
    .input-wrapper:first-child{
      input{
        padding-top:40px;
      }
    }
    .msgcode-wrapper{
      input{
        width:330px;
        float: left;
      }
      i.del{
        right: 110px;
      }
      .aboutcode{
        float: right;
        cursor: pointer;
        width:90px;
        height:40px;
        line-height: 40px;
        border: 1px solid #1CD7D1;
        box-sizing: border-box;
        border-radius: 2px;
        font-size: 14px;
        color: #1CD7D1;
        text-align: center;
        margin-top:38px;
      }
    }
    .submit-wrapper{
      .submit{
        margin-top:38px;
      }
    }
    .register-tip{
      padding-top:10px;
      font-size: 12px;
      color: #A5A8A8 ;
      span{
        color: #1CD7D1;
      }
    }
    .go-login-tips{
      float: right;
      margin-top:20px;
      font-size: 14px;
      color: #A5A8A8;
      line-height: 14px;
      span{
        color:#1CD7D1;
      }
    }
  }

</style>
